
.food-area{
    padding: 4rem 10rem 5rem;
}
.food-area .container{
    display: grid;
    grid-template-columns: 56% 44%;
    
}
.food-area .text{
    padding-left: 1rem;
    padding-right: 5rem;
    
}
.food-area .text h4{
    font-weight: 400;
    color:#009DFF;
}
.food-area .text h2{
    font-weight: 400;
    font-size: 3rem;
    margin-block: 0.3rem;
}
.text p{
    font-weight: 300;
    color:#4D4D4D;
    line-height: 1.7;
    font-size: 1.1rem;
}
.food-area .images{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-left: 1rem;
}
.food-area .images img:first-child{
    align-self: start;
    width: 90%;
    object-fit: contain;
}
.food-area .images img:last-child{
    align-self: end;
    margin-top: 2.5rem;
    width:90%;
    object-fit: contain;
    
}