.login.container{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(240, 248, 255,0.9);
    padding: 4rem 7rem;
}
.login-container{
    width:380px;
    height: 440px;
    margin:auto;
    background-color: white;
    padding: 1rem 2.5rem;
}
.login-container h3{
    text-align: center;
    font-size: 2.8rem;
    margin-bottom: 0.9rem;
}
input{
    width:100%;
    margin: 0.7rem auto 1rem;
    border-radius: 5px;
}
button[type="submit"]{
    margin-top: 2rem;
    background-color: rgb(0, 170, 255);
    padding:0.4rem 0.7rem;
}
