header{
    display: grid;
    grid-template-columns: 43% 15% 28% 14%;
    background-color:transparent;
    padding-inline: 6rem;
    overflow: hidden;
    transition: all 0.5s;
    color:black;
    position: absolute;
    z-index: 1;
    top:0;
    right: 0;
    left: 0;
}

.sticky{
    position: fixed;
    top:0;
    right: 0;
    left: 0;
    background-color: rgb(8, 8, 8);
    color:white;
}
#navigation{
    display: flex;
    justify-content: space-between;
    color: black;
    height: 110px;
    align-items: center;
    padding-inline:6rem;
    list-style: none;
}
nav ul{
    margin-block: 0;
}
nav a{
    color:white;
    text-decoration: none;
   
}
nav a:hover{
    padding-bottom: 7px;
    border-bottom-style: solid;
    border-bottom-width: 3.1px;
    width: fit-content;
}
nav a.active{
    padding-bottom: 7px;
    border-bottom-style: solid;
    border-bottom-width: 3.1px;
    width: fit-content;
}
nav a:active{
    padding-bottom: 7px;
    border-bottom-style: solid;
    border-bottom-width: 3.1px;
    width: fit-content;
}
.logo{
    display: flex;
    justify-content: center;
    align-items: center;
}
.social_links ul{
    display: flex;
    justify-content: flex-end;
    margin-top: 3rem;
    color:rgb(255, 255, 255, 0.7);

}
.social_links ul li:hover{
    color:#fff;
}
.social_links ul li{
    margin-inline: 1em;
    list-style: none;
}
.social_links ul li:last-of-type{
    margin-right: 3rem;
}
.logo img{
    width:100px;
}
.book_btn{
    background-color: rgb(0, 181, 253);
    height: 38px;
    width:140px;
    text-align: center;
    padding-top: 8px;
    align-self: center;
}
.book_btn a{
    text-decoration: none;
    color:white;
    font-size: 1rem;
}
@media (max-width:1330px){
    #navigation{
        padding-inline: 2rem;
    }
}

@media (max-width:910px){
    #navigation{
        padding-inline:0.5rem;
    }
}
