.carousel{
    height: 100vh;
    position: relative;
    display: flex;
    color:white;
}
.carousel-img{
    position: absolute;
    inset: 0;
    z-index: -1;
    width:100%;
    height: 100%;
    object-fit: cover;
}
.carousel-text{
    margin-block: auto;
    margin-inline: auto;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.carousel h1{
    font-size: 4rem;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 0.5rem;
    
}
.carousel h2{
    font-weight: 400;
    margin-top: 0;
    font-size: 2rem;
}