*{
    box-sizing: border-box;
}
.about-area{
    padding: 6rem 6rem;
    height: 100vh;
}
/* Banner img component style */
.banner-img{
    max-height: 90vh;
    overflow: hidden;
}
.banner-img img{
    width:100%;
    height: 100%;
    object-fit: cover;
}
/* Banner img component style END */
.container{
    display: grid;
    grid-template-columns: 44% 56%;
    
}
.text{
    padding-left: 5rem;
    padding-right: 1rem;
    
}
.text h4{
    font-weight: 400;
    color:#009DFF;
}
.text h2{
    font-weight: 400;
    font-size: 3rem;
    margin-block: 0.3rem;
}
.text p{
    font-weight: 300;
    color:#4D4D4D;
    line-height: 1.7;
    font-size: 1.1rem;
}
.images{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-right: 1rem;
}
.images img:first-child{
    align-self: start;
    width: 90%;
    object-fit: contain;
}
.images img:last-child{
    align-self: end;
    margin-top: 2.5rem;
    width:90%;
    object-fit: contain;
    
}

.more{
    text-decoration: none;
    color:#4D4D4D;
    padding-bottom: 0.4rem;
    border-bottom: 1px solid black;
    width: fit-content;
}