.popup_box{
    position: fixed;
    inset:0;
    background-color: rgb(255,255,255,0.3);
    display: none;
    z-index: 100;
}
.popup_inner{
    width:601;
    height: 361;
    padding: 2rem 4rem;
    text-align: center;
    margin-inline:auto;
    justify-self: center;
    align-self: center;
    background-color: white;

}
.grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:1.5rem;

}
input{
    display: inline-block;
    padding: .5rem .6rem;
}
.visible{
    display: flex;
}